import '../App.css';
import { Link } from "react-router-dom";

const Nav = () => {
	return (
		<div className='header'>
			<div>
				<h1>Shopping Cart</h1>
			</div>
			<div className='nav-links'>
				<div><Link to='/' className='linkD'>Home</Link></div>
				<div><Link to='/shop' className='linkD'>Shop</Link></div>
			</div>
		</div>
	)
}

export default Nav;