import '../App.css';
import { useState, useEffect } from "react"

const Item = (props) => {
	useEffect( () => {
		fetchItem()
	}, [])

	const [item, setItem] = useState({
		names: [{},{},{},{},{},{},{},{},{},{}],
		sprites: {}
	})

	const [value, setValue] = useState(0)

	const fetchItem = async () => {
		const data = await fetch(`https://pokeapi.co/api/v2/item/${props.name}`)

		const itemG = await data.json()
		setItem(itemG)
		console.log(itemG)
	}

	const increase = () => {
		setValue(value + 1)
	}

	const decrease = () => {
		if (value > 0) {
			setValue(value - 1)
		}
	}

	const reset = (e) => {
		setValue(0)
		e.preventDefault();
	}

	const onChange = (e) => {
    const re = /^[0-9\b]+$/;

    // if value is not blank, then test the regex

    if (e.target.value === '' || re.test(e.target.value)) {
       setValue(e.target.value)
    }
	}


	return (
		<div className='container'>
			<form onSubmit={reset}>
				<div className='container'>
					<div>
						<h3>{item.names[7].name}</h3>
					</div>
					<div>
						<img src={item.sprites.default} alt={item.name} width='80px' height='80px'></img>
					</div>
				</div>
				<div className='horizontal-container'>
					<button type='button' className='blue-button' onClick={decrease}>-</button>
					<input type='tel' className='small' value={value} onChange={onChange}/>
					<button type='button' className='blue-button' onClick={increase}>+</button>
				</div>
				<div>
					<button type='submit' className='green-button' onClick={() => props.addItem(item.names[7].name, value)}>Add to cart</button>
				</div>
			</form>
		</div>
	)
}

export default Item;