import { useState, useEffect } from "react"
import { Link } from "react-router-dom";
import Item from "./Item"
import '../App.css';

const Shop = (props) => {
	useEffect( () => {
		fetchItems()
	}, [])

	const [items, setItems] = useState([])

	/*const [cart, setCart] = useState([])

	const addItem = (item, number) => {
		const tempArray = cart;
		tempArray.push({name: item, quantity: number})
		setCart(tempArray)
		console.log(cart)
	}*/

	const fetchItems = async () => {
		const data = await fetch('https://pokeapi.co/api/v2/item?limit=10')

		const itemsP = await data.json()
		setItems(itemsP.results)
		console.log(items)
	}

	return (
		<div className='container'>
			<div className='shop-header'>
				<div>
					<h1>Items in cart: {props.number}</h1>
				</div>
				<div>
					<Link to='/cart' state={{value: props.cart}}><button className='red-button'>Go to cart</button></Link>
				</div>
			</div>
			<div className='shop-container'>
				{items.map((item) => (
					<div key={item.name} className='shop-item'>
						<Item name={item.name} addItem={props.addItem}/>
					</div>
				))}
			</div>
		</div>
	) 
}

export default Shop;