import './App.css';
import { useState } from "react"
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import Home from './components/Home';
import Shop from './components/Shop';
import Cart from './components/Cart';
import Nav from './components/Nav';

const App = () => {
	const [cart, setCart] = useState([])

	const [number, setNumber] = useState(0)

	const addItem = (item, number) => {
		const tempArray = cart;
		let found = false;
		for (let i = 0; i < tempArray.length; i++) {
			if (tempArray[i].name === item) {
				tempArray[i].quantity = number + tempArray[i].quantity;
				setCart(tempArray)
				found = true;
			}
		} 

		if (!found) {
			tempArray.push({name: item, quantity: number})
			setCart(tempArray)
			setNumber(tempArray.length)
		}
		
		console.log(cart)
	}

  return (
    <Router>
			<div className='main-container'>
				<Nav/>
				<Routes>
					<Route path='/' element={<Home/>}/>
					<Route path='/shop' element={<Shop addItem={addItem} cart={cart} number={number}/>}/>
					<Route path='/cart' element={<Cart/>}/>
				</Routes>
			</div>
		</Router>
  )
}

export default App;
